<template>
  <div class="ip-lookup-container">
    <h2>IP地址信息</h2>
    <div v-if="loading" class="loading">正在获取IP地址...</div>
    <div v-else-if="error" class="error">{{ error }}</div>
    <div v-else class="ip-details">
      <p>公网IP地址: <strong>{{ ipAddress }}</strong></p>
      <div class="geographic-info">
        <h3>地理位置详情</h3>
        <table>
          <tbody>
            <tr>
              <td>国家：</td>
              <td>{{ geographicInfo.country }} ({{ geographicInfo.countryCode }})</td>
            </tr>
            <tr>
              <td>地区：</td>
              <td>{{ geographicInfo.regionName }}</td>
            </tr>
            <tr>
              <td>城市：</td>
              <td>{{ geographicInfo.city }}</td>
            </tr>
            <tr>
              <td>邮政编码：</td>
              <td>{{ geographicInfo.zip }}</td>
            </tr>
            <tr>
              <td>经纬度：</td>
              <td>{{ geographicInfo.lat }}, {{ geographicInfo.lon }}</td>
            </tr>
            <tr>
              <td>时区：</td>
              <td>{{ geographicInfo.timezone }}</td>
            </tr>
            <tr>
              <td>互联网服务提供商：</td>
              <td>{{ geographicInfo.isp }}</td>
            </tr>
            <tr>
              <td>组织：</td>
              <td>{{ geographicInfo.org }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <button @click="fetchIPAddress" :disabled="loading">重新获取</button>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'IPLookupTool',
  data() {
    return {
      ipAddress: '',
      loading: false,
      error: null,
      geographicInfo: {}
    }
  },
  mounted() {
    this.fetchIPAddress()
  },
  methods: {
    async fetchIPAddress() {
      this.loading = true
      this.error = null
      try {
        const response = await axios.get('https://api64.ipify.org?format=json')
        console.log(response.data)
        this.ipAddress = response.data.ip
        
        const geographicInformationResponse = await axios.get(`http://ip-api.com/json/${this.ipAddress}?lang=zh-CN`)
        this.geographicInfo = geographicInformationResponse.data
        console.log(this.geographicInfo)
      } catch (err) {
        this.error = '获取IP地址失败：' + err.message
      } finally {
        this.loading = false
      }
    }
  }
}
</script>

<style scoped>
.ip-lookup-container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  font-family: Arial, sans-serif;
}

.loading, .error {
  color: #666;
  margin: 20px 0;
}

.ip-details {
  margin: 20px 0;
}

.geographic-info {
  background-color: #f4f4f4;
  border-radius: 8px;
  padding: 15px;
  margin-top: 15px;
  text-align: left;
}

.geographic-info table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 10px;
}

.geographic-info td:first-child {
  font-weight: bold;
  width: 40%;
  color: #333;
}

.geographic-info td:last-child {
  color: #666;
}

button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}
</style>